* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header {
  padding: 1rem 0;
  background-color: lightblue;
  color: white;
  text-align: center;
  font-size: 2rem;
  max-width: 600px;
  margin: auto;
}
.list {
  max-width: 600px;
  margin: auto;
}
.list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
}

.title {
  font-size: 20px;
}
.date {
  color: #999;
}

.list li div:nth-of-type(2) {
  width: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.btn_good,
.btnAdd {
  border: none;
  outline: none;
  background-color: #666;
  color: white;
  padding: 0.25rem;
  border-radius: 0.25rem;
}

.addcon {
  border: 1px solid #666;
  min-width: 400px;
  width: 500px;
  max-width: 500px;
  margin: 2rem auto;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.inputTitle {
  width: 250px;
  padding: 0.25rem;
}

/* modal */
.modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .con {
  background-color: white;
  padding: 1rem;
  border-radius: 1rem;
  min-height: 300px;
  width: 50%;
  max-width: 500px;
  position: relative;
}
.modal h1 {
  text-align: center;
}
.modal p {
  padding: 1rem 0;
}
.modal .close {
  position: absolute;
  top: 0;
  right: -50px;
}
